#works{
    margin:0 auto;
    min-height: calc(100vh - 5rem);
    width:100vw;
    max-width:60rem;
    display:flex;
    flex-direction: column;
    align-items:center;
    justify-content: center;
    text-align: center;
    padding: 0 1rem;
}
.workstitle{
    margin:1.25rem 0;
    font-size: 3rem;
}
.worksDesc{
    font-weight:300;
    font-size: 1rem;
    max-width:45rem;
}
.worksImgs{
    display:flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    max-width: 65rem;
    width: 100vw;
}
.worksImg{
    object-fit: cover;
    height: 20rem;
    margin:0.5rem;
}
.workBtn{
    padding:0.5rem 2.5rem;
    border: none;
    border-radius: 2rem;
    background: white;
    margin:3rem 0;
    font-size: 1rem;
}
@media screen and (max-width:585px) {
    .worksImg {
        height:47vw;
    }
}